@import url(https://fonts.googleapis.com/css2?family=Kiwi+Maru:wght@300&family=M+PLUS+Rounded+1c&family=Mrs+Saint+Delafield&display=swap);
.modal-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  min-height: 120%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-container-open {
  position: relative;
  margin: 0 auto;
  border: solid;
  border-color: #66bb6a;
  border-radius: 20px;
  width: 70%;
  margin-top: 25px;
  margin-bottom: 25px;
  padding: 10px;
  background-color: white;
}
.modal-container-edit {
  margin: 0 auto;
  max-width: 100%;
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
}
.modal-container-edit-elements {
  border: solid;
  border-color: #66bb6a;
  border-radius: 15px;
  padding: 15px;
  margin: 10px;
}
.modal-container-edit-elements ul {
  text-align: left;
}
.modal-container-add {
  position: relative;
  border: solid;
  border-color: #66bb6a;
  margin: 0 auto;
  max-width: 50%;
  border-radius: 15px;
  padding: 15px;
}

.modal-container-add-elements {
  display: flex;
}
.modal-container-add-elements-form div {
  display: flex;
}

.modal-container-open-button {
  position: relative;
  display: inline-flex;
  font-size: 0.875rem;
  min-width: 64px;
  margin: 10px 5px 10px 5px;
  color: #fff;
  line-height: 1.75;
  border: none;
  cursor: pointer;
  box-shadow: 0px 3px 1px -2px rgb(0 0 0 / 20%),
    0px 2px 2px 0px rgb(0 0 0 / 14%), 0px 1px 5px 0px rgb(0 0 0 / 12%);
  padding: 6px 46px 6px 16px;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    box-shadow 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms,
    border 250ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
  align-items: center;
  border-radius: 20px;
  letter-spacing: 0.02857em;
  background-color: #66bb6a;
}
.modal-container-open-button:active {
  -webkit-transform: translate(0, 2px);
  transform: translate(0, 2px);
  border-bottom: none;
}
.modal-container-open-button::before {
  content: "";
  position: absolute;
  right: 6px;
  background-color: #cae6b7;
  width: 20px;
  height: 20px;
  border-radius: 20px;
}
.modal-container-open-button::after {
  content: "";
  position: absolute;
  right: 13px;
  border-top: 2px solid #2b550e;
  border-right: 2px solid #2b550e;
  width: 5px;
  height: 5px;
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.modal-container-open-button:hover {
  background-color: #39993c;
}

.calendar-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.react-calendar {
  width: 700px;
  max-width: 100%;
  min-width: 400px;
  height: 350px;
  background-color: #fff;
  color: #222;
  border-radius: 8px;
  box-shadow: 0 12px 24px rgba(0, 0, 0, 0.2);
  font-family: Arial, Helvetica, sans-serif;
  line-height: 2.125em;
}
.react-calendar__navigation button {
  color: #66bb6a;
  min-width: 44px;
  background: none;
  font-size: 16px;
  margin-top: 8px;
}
.react-calendar__navigation button:enabled:hover,
.react-calendar__navigation button:enabled:focus {
  background-color: #f8f8fa;
}
.react-calendar__navigation button[disabled] {
  background-color: #f0f0f0;
}
abbr[title] {
  text-decoration: none;
}
/* .react-calendar__month-view__days__day--weekend {
  color: #d10000;
 } */
.react-calendar__tile:enabled:hover,
.react-calendar__tile:enabled:focus {
  background: #f8f8fa;
  color: #66bb6a;
  border-radius: 6px;
}
.react-calendar__tile--now {
  background: #66bb6a;
  border-radius: 6px;
  font-weight: bold;
  color: #f8f8fa;
}
.react-calendar__tile--now:enabled:hover,
.react-calendar__tile--now:enabled:focus {
  background: #f8f8f8;
  border-radius: 6px;
  font-weight: bold;
  color: #66bb6a;
}
.react-calendar__tile--hasActive:enabled:hover,
.react-calendar__tile--hasActive:enabled:focus {
  background: #f8f8fa;
}
.react-calendar__tile--active {
  background: #66bb6a;
  border-radius: 6px;
  font-weight: bold;
  color: white;
}
.react-calendar__tile--active:enabled:hover,
.react-calendar__tile--active:enabled:focus {
  background: #66bb6e;
  color: white;
}
.react-calendar--selectRange .react-calendar__tile--hover {
  background-color: #f8f8fa;
}
.react-calendar__tile--range {
  background: #f8f8fa;
  color: #66bb6a;
  border-radius: 0;
}
.react-calendar__tile--rangeStart {
  border-top-right-radius: 0;
  border-bottom-right-radius: 0;
  border-top-left-radius: 6px;
  border-bottom-left-radius: 6px;
  background: #66bb6a;
  color: white;
}
.react-calendar__tile--rangeEnd {
  border-top-left-radius: 0;
  border-bottom-left-radius: 6px;
  border-top-right-radius: 6px;
  border-top-left-radius: 6px;
  border-bottom-right-radius: 6px;
  background: #66bb6a;
  color: white;
}

.App {
  position: relative;
  font-family: "M PLUS Rounded 1c", sans-serif;
  justify-content: center;
  text-align: center;
}

